.trust {
  padding-top: 89px;
  padding-bottom: 104px;
  background-color: $dark-bg-color;
  display: flex;
  flex-direction: column;
  align-items: center;
  .trust__header {
    max-width: 947px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 54px;
  }
  .trust__image {
    width: 457px;
    height: auto;
    img {
      width: 100%;
    }
  }
  .trust__title {
    text-align: center;
    h1 {
      color: $alt-text-color;
      line-height: 107px;
    }
  }
  .trust__caption {
    max-width: 1097px;
    margin-bottom: 39px;
    h2 {
      color: $alt-text-color;
      font-family: 'Cormorant', sans-serif;
      font-weight: 500;
      font-size: 50px;
      text-align: center;
      text-transform: uppercase;
      line-height: 60px;
    }
  }
  .trust__slider {
    width: 1382px;
    .slider__card {
      height: 521px;
      position: relative;
      .slide__image {
        img {
          object-fit: cover;
          height: 100%;
          width: 100%;
        }
      }
      .slider__caption {
        position: absolute;
        bottom: 30px;
        width: 100%;
        text-align: center;
        p {
          font-family: 'Gilroy', sans-serif;
          color: $alt-text-color;
          font-weight: 600;
          font-size: 1.75rem;
        }
      }
    }
  }
  .slider {
    padding: 0 43px;
    .itc-slider-items {
      --gap: 24px;
      gap: var(--gap);
      background-color: $alt-bg-color;
    }
    .itc-slider-item {
      flex: 0 0 calc(33.3333333333% - var(--gap) * 2 / 3);
    }
    .itc-slider-wrapper {
      background-color: $dark-bg-color;
    }
    .itc-slider-btn {
      background-color: rgba(0, 0, 0, 0);
      width: 26px;
      height: 39px;
    }
    .itc-slider-btn-prev::before {
      background-image: none;
    }
    .itc-slider-btn-next::before {
      background-image: none;
    }
  }
  
}