.digits {
  padding-top: 150px;
  padding-bottom: 85px;
  .digits__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .digits__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
    .digits__image {
      margin: -10px;
    }
  }
  .dtable {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 25px;
    margin-bottom: 70px;
    .dtable__cell {
      border: 1px solid $main-text-color;
      padding-top: 30px;
      padding-right: 30px;
      padding-bottom: 40px;
      padding-left: 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .dtable__digits {
      p {
        display: inline-block;
        padding-right: 10px;
        font-family: 'Miama Nueva', sans-serif;
        font-weight: 400;
        font-size: 40px;
        font-style: italic;
        color: transparent;
        background-clip: text;
        background-image: linear-gradient(to right, #FFDE9A, #CEA764);
        letter-spacing: -3px;
        line-height: 70px;
      }
    }
    .dtable__caption {
      margin-top: -10px;
      p {
        font-size: 1.375rem;
      }
    }
  }
  .digits__button {
    width: 100%;
    text-align: center;
    .digits__btn {
      width: calc(33.33% - 25px * 2 / 3);
      text-transform: uppercase;
      background-color: rgba(255, 255, 255, 0);
      border: 1px solid $main-text-color;
      padding: 30px 0;
      font-size: 1.25rem;
    }
  }
}