.product {
  margin-bottom: 201px;
  .product__content {
    display: grid;
    grid-template-columns: 527px auto;
    border-top: 1px solid $main-text-color;
    border-bottom: 1px solid $main-text-color;
  }
  .product__image {
    width: 100%;
    img {
      width: 100%;
      height: calc(100% + 2px);
      object-fit: cover;
    }
  }
  .product__caption {
    padding-top: 118px;
    padding-bottom: 126px;
    padding-left: 52px;
    //flex-grow: 1;
    position: relative;
    .product__title {
      margin-bottom: 50px;
    }
    img {
      position: absolute;
      top: 220px;
      right: -30px;
    }
    .product__desc_1 {
      width: 80%;
      font-size: 1.75rem;
      font-weight: 500;
      margin-bottom: 20px;
    }
    .product__desc_2 {
      width: 80%;
      font-size: 1.375rem;
      font-weight: 400;
      margin-bottom: 60px;
    }
    .product__button {
      text-transform: uppercase;
      font-size: 1.25rem;
      padding: 30px 130px;
      background-color: $main-bg-color;
      border: 1px solid $main-text-color;
    }
  }
}