.catalogue {
  padding-top: 85px;
  //padding-bottom: 100px;
  .catalogue__header {
    width: 100%;
    margin-bottom: 60px;
  }
  .catalogue__image {
    margin: -15px;
  }
  .catalogue__title h1 {
    line-height: 107px;
  }
  .ctable {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 220px);
    gap: 25px;
    padding-bottom: 200px;
    .ctable__cell {
      position: relative;
    }
    .ctable__image {
      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }
    .ctable__caption {
      display: flex;
      justify-content: center;
      position: absolute;
      top: 50%;
      width: 100%;
      text-align: center;
      transform: translate(0, -50%);
      z-index: 10;
      p {
        font-size: 26px;
        font-weight: 500;
        width:80%;
        color: $alt-text-color;
      }
    }
    .ctable__bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(27, 27, 27, 0.15);
      z-index: 1;
    }
  }
}