.feedback {
  padding-top: 90px;
  padding-bottom: 106px;
  background-color: $dark-bg-color;
  .ftable {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    .ftable__panel {
      display: flex;
      flex-direction: column;
    }
    .ftable__image {
      margin-left: -60px;
      margin-bottom: -30px;
    }
    .ftable__title {
      margin-bottom: 60px;
      h1 {
        color: $alt-text-color;
        line-height: 107px;
      }
    }
    .ftable__caption {
      margin-bottom: 50px;
      p {
        color: $alt-text-color;
        font-size: 1.75rem;
        font-weight: 500;
      }
    }
    .ftable__form {
      padding-right: 40px;
      flex: 1 0 auto;
      .form {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
      }
      .form__input {
        display: block;
        width: 100%;
        padding: 16px 25px;
        margin-bottom: 25px;
        box-sizing: border-box;
        background-color: $main-bg-color;
        border: none;
        font-weight: 300;
        font-size: 1.375rem;
      }
      .form__button {
        width: 100%;
        text-align: center;
        .form__btn {
          width: 100%;
          text-transform: uppercase;
          padding: 30px 0;
          font-size: 1.25rem;
          color: $alt-text-color;
          background-color: $dark-bg-color;
          border: 1px solid $alt-text-color;
        }
      }
    }
    .ftable__picture img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      box-sizing: border-box;
    }
  }
}