* {
  margin: 0;
  padding: 0;
  font-family: Gilroy, sans-serif;
  color: $main-text-color;
}
.container {
  margin: 0 auto;
  width: min(100%, $container-default-width);
}
.container-fluid {
  width: 100%;
}
.subtitle {
  font-family: 'Cormorant', sans-serif;
  font-weight: 500;
  font-size: 100px;
  text-transform: uppercase;
}
