.header {
  .navbar {
    padding: 14px 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .navbar__logo {
    height: 64px;
    margin-right: 11px;
    img {
      height: 100%;
      width: auto;
    }
  }
  .navbar__menu {
    font-size: 1.25rem;
    margin-right: 18px;
    ul {
      list-style-type: none;
      display: flex;
      flex-direction: row;
      gap: 20px;
      li a {
        text-decoration: none;
        color: $main-text-color;
        &:hover {
          color: $hover-link-color;
        }
      }
    }
  }
  .navbar__lang {
    margin-right: 26px;
    font-size: 1.25rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    span {
      margin-right: 4px;
    }
    img {
      width: 12px;
    }
  }
  .navbar__right {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .navbar__lock {
    margin-right: 15px;
  }
  .navbar__button {
    font-size: 17px;
    text-transform: uppercase;
    background-color: $main-bg-color;
    padding: 17px 40px;
    border: 1px solid #232323;
  }
}
