.news {
  padding: 170px 0;
  .news__image {
    margin-bottom: -30px;
    img {
      width: 331px;
      height: auto;
    }
  }
  .news__content {
    padding: 0 10px;
    box-sizing: border-box;
    h1 {
      margin-left: 5px;
      margin-bottom: 60px;
      line-height: 121px;
    }
  }
  .news__items {
    display: grid;
    grid-template-columns: repeat(4, minmax(250px, 1fr));
    gap: 25px;
    margin-bottom: 70px;
  }
  .ncard {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    .ncard__content {
      margin-bottom: 30px;
    }
    .ncard__img {
      width: 100%;
      margin-bottom: 20px;
      img {
        width: 100%;
        height: auto;
      }
    }
    .ncard__availability {
      font-weight: 400;
      font-size: 1rem;
      margin-bottom: 12px;
    }
    .ncard__name {
      font-weight: 500;
      font-size: 1.375rem;
      margin-bottom: 5px;
    }
    .ncard__caption {
      font-size: 1.25rem;
      font-weight: 300;
    }
    .ncard__price_val {
      font-size: 1.75rem;
      font-weight: 500;
    }
  }
  .news__button {
    width: 100%;
    text-align: center;
    .news__btn {
      background-color: $main-bg-color;
      text-transform: uppercase;
      font-size: 1.25rem;
      border: 1px solid $main-text-color;
      padding: 31px 135px;
    }
  }
}